<template>
  <div>
    <Header></Header>
    <section id="main">
      <div class="container">
        <div class="row">
          <div class="col-8 col-6-medium col-12-small">
            <section>
              <!--  Team -->
                <h2>Meet the Team</h2>
                <p>
                  LaravelDash is built by a team of engineers who share a passion for making web development feel effortless. We believe that writing beautiful apps should be joyful and fun. We're building a platform for the future.
                </p>
              <div class="card" v-for="contributor in data">
                <div class="box">
                  <div class="img">
                    <img :src="contributor.avatar_url">
                  </div>
                  <h2>{{ contributor.login}}<br><strong>{{ contributor.contributions}} Commits</strong></h2>
                  <button class="btn btn-lg btn-block-github" @click="github(contributor.html_url)"><i class="fa fa-github"></i> GitHub</button>
                </div>
              </div>
              <!-- end team -->
              <!-- Sponsors -->
               <h2 style="margin-top:55px !important;">One-time Donations</h2>
               <p>We accept donations through these channels:</p>
               <ul class="donation">
                 <li><img @click="linkCrypto='31noxeTtz9nkYkQngcYD4PYJ5EMtxDV75S'" :src="require('../assets/bitcoin.png')" width="54" height="54"><b>BTC</b></li>
                 <li><img @click="linkCrypto='0x6bDa55C6Cad662C1851E1ab8B340e00E0590A1f5'" :src="require('../assets/ech.png')" width="54" height="54"><b>ETH</b></li>
                 <li><img @click="linkCrypto='qq5fdpjjvypww2emelpfpcayq45wqjxggyg64za2mh'" :src="require('../assets/bch.png')" width="54" height="54"><b>BCH</b></li>
                 <li><img @click="linkCrypto='MEx8d9GTCzo4A2RuD5xd7gAd5DjyVyjqUh'" :src="require('../assets/ltc.png')" width="54" height="54"><b>LTC</b></li>
               </ul>
              <!-- end Sponsors -->
              <mark v-if="linkCrypto.length>0" id="address">{{linkCrypto}}</mark>
            </section>
          </div>
          <div class="col-4 col-6-medium col-12-small">
            <img :src="require('../assets/banner_ads_with.png')" width="300" style="height:auto;margin-top:30px;" />
            <h2 style="margin-top:60px !important;line-height:2.13rem;">You can have a major impact</h2>
            <p>
              If you have been working on small projects that have not had much attention, then contributing to famous open source projects can give you the opportunity to have a larger impact.
              By making such contributions, you are potentially going to have a major impact with the work that you do, which can be a very fulfilling experience. No matter how small your contributions are, you are also going to have a feeling of significance, simply because you know that your work has made that project that you love a little better.
            </p>
            <h2 style="margin-top:60px !important;line-height:2.13rem;">Why Open Source Is Good ?</h2>
            <p>
              When using open source software you can usually dive deep into the code of the product you’re using and change it however you want. That means that you can also tweak such software for your own specific needs, fork it in case you see it should be done in another way and modify it endlessly for free. But the most important advantage of all these possibilities is that it is allowed to do all of that by the open source licenses.
              The proprietary tools, on the contrary, may have additional limitations on its use, which means that you might need to dive deep into user agreements and privacy policy to be sure that everything you do with such products is allowed.
            </p>
          </div>

        </div>
      </div>
    </section>
  </div>
</template>

<script>
  import axios from 'axios';
  import Header from "./Header";
  export default {
    name: 'HelloWorld',
    data() {
      return {
        data: [],
        linkCrypto: '',
      }
    },
    components: {
      'Header': Header
    },
    methods:{
      github:(url)=>{
        window.open(url);
      },
    },
    mounted() {
      // Make a request for a user with a given ID
      axios.get('https://api.github.com/repos/getspooky/laravelDash/contributors')
        .then((response)=>{
          // handle success
          this.data = [...response.data];

          console.log(this.data);

        })
        .catch(function (error) {
          // handle error
          console.log(error);
        });

    }
  }
</script>

<style scoped>

  .mb-30{
    margin-bottom:50px !important;
  }

  h2 {
    color: #4C4C4C;
    font-size: 30px;
    font-weight: 700;
    margin-top:20px;
    margin-bottom:20px;
    font-family: 'Raleway', sans-serif;
  }

  p{
    font-size:20px;
  }

  .ion-minus{
    padding:0px 10px;
  }

  ul.donation li{
    margin-left:12px !important;
  }

  #address{
    background-color:#000;color:#fff;
    padding:13px;
    position:relative !important;
    float:left;
    width:370px;
    margin-left:45px;
    margin-top:20px;
    text-align:center;
    display:block !important;
  }

  ul.donation li b{
    color: #34495e;
    position:relative;
    top:-20px;
    left:05px;
    font-family:"Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
    font-size:17px !important;
  }

  .btn-block-github{
    position:relative;
    width:100%;
    margin:10px 0px 0px 0px;
    background-color:rgb(37, 33, 34);
  }

  .card {
    position:relative;
    width:300px;
    min-height:280px !important;
    margin-right:40px !important;
    top:30px;
    margin-bottom:80px;
    display:inline-block;
    background:#fff;
    box-shadow:0 20px 50px rgba(0,0,0,.1);
    border-radius:10px;
    transition:0.5s;
  }
  .card:hover {
    box-shadow:0 30px 70px rgba(0,0,0,.2);
  }
  .card .box {
    position:absolute;
    top:50%;
    left:0;
    transform:translateY(-50%);
    text-align:center;
    padding:20px;
    box-sizing:border-box;
    width:100%;
  }
  .card .box .img {
    width:120px;
    height:120px;
    margin:0 auto;
    border-radius:50%;
    overflow:hidden;
  }
  .card .box .img img {
    width:100%;
    height:100%;
  }
  .card .box h2 {
    font-size:20px;
    color:#262626;
    margin:20px auto;
  }
  .card .box h2 span {
    font-size:14px;
    background:#e91e63;
    color:#fff;
    display:inline-block;
    padding:4px 10px;
    border-radius:15px;
  }
  .card .box p {
    color:#262626;
  }
  .card .box span {
    display:inline-flex;
  }
  .card .box ul {
    margin:0;
    padding:0;
  }

  ul.donation{
    display:block;
    list-style:none;
  }

  ul.donation li{
    float:left;
  }

</style>



